<template>
  <div style="min-width: 900px;">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>集群对讲应用</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="userlist" stripe>
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column label="集群对讲终端ID" prop="deviceName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="集群对讲终端IP" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.deviceIp == null ||  scope.row.deviceIp === '' ? '-' : scope.row.deviceIp}}</span>
          </template>
        </el-table-column>
        <el-table-column label="密钥使用量" prop="keyNum" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.keyNum == null ||  scope.row.keyNum === '' ? '-' : scope.row.keyNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="上次获取密钥时间" prop="updateTime" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <span>{{scope.row.updateTime == null ||  scope.row.updateTime === '' ? '-' : scope.row.updateTime}}</span>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        userlist: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`intercom/getIntercomStatus/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取用户信息失败！');
        this.userlist = res.data.list;
        this.total = res.data.total;
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getUserList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getUserList()
      },
    }
  }
</script>
<style lang="less" scoped>

</style>
